
#data-list {
  margin-left: 1rem;
  margin-right: 1rem;
  padding: 0;
}
#data-list .dropdown-title i {
  color: red;
}
#data-list .li .md-button-ghost {
  background: none;
}
#data-list span {
  color: #201547;
  font-weight: 500;
}
#data-list i {
  color: #00b7bd;
}
#data-list .md-active span {
  color: white;
  font-weight: 500;
}
#data-list .md-active i {
  color: white;
  font-weight: 500;
}
#data-list .md-active .li-container {
  background-color: #00b7bd;
}
#data-list .li-expand:after {
  height: 0;
}
.add-obs-button {
  text-decoration: none;
  color: #26a69a;
  background-color: #f5f5f5;
  text-align: center;
  letter-spacing: .5px;
  border: none;
  border-radius: 2px;
  height: 36px;
  line-height: 36px;
  padding: 0 2rem;
  text-transform: uppercase;
  margin: .5rem;
  font-weight: 600;
}



